footer{
    border-top: 1px solid #000;
    margin-top: 3rem; 
    padding: 2rem 0 ;
    display: flex;
    justify-content: space-between;
    align-items: center;
}

footer p{
    font-weight: 500;
    max-width: 600px;
    margin-right: 0.5rem;
}

@media (max-width:576px) {
    footer p{
        font-size: .8rem;
    }
}